import React from "react"
import { motion } from "framer-motion"
import styled from "styled-components"
import { Heading, Rating } from "components"
import { baseStyles } from "styles/base"
import { connect } from "react-redux"
import { getPhotoUrl, placeholderImage } from "helpers"
import { Row, Col } from "antd"
import { Trans } from "@lingui/macro"
import { LazyImage } from "react-lazy-images"

const StyledCard = styled(motion.div).attrs({
	whileTap: { scale: 0.94 },
	whileHover: { scale: 1.03 }
})`
	border-radius: 10px;
	box-shadow: ${baseStyles.boxShadow.main};
	background-color: #fff;
	height: 300px;
	max-height: 300px;
	.details {
		text-align: center;
		padding: 0;
		article.ant-typography {
			width: 90%;
			margin: 0 auto;
		}
		h4 {
			font-size: 1em;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow-x: hidden;
			margin-bottom: 0.5em;
		}
	}
	.image-wrapper {
		max-height: 200px;
		overflow-y: hidden;
		margin-bottom: 1.8em;
		> img {
			min-height: 200px;
			object-fit: cover;
			border-radius: 8px 8px 0 0;
		}
	}
	.rating-section,
	.price-section {
		.ant-rate-star {
			margin: 0;
		}
	}
	.rating-section {
		font-size: 0.8em;
	}
`

function PlaceCard({ data = {}, photo, ...props }) {
	const photoData = data.photos || {}
	const thePhoto = photoData ? getPhotoUrl(photoData.reference) : placeholderImage

	return (
		<StyledCard {...props}>
			<section className="details">
				<div className="image-wrapper">
					<LazyImage
						src={thePhoto}
						alt={data.name}
						placeholder={({ imageProps, ref }) => <img ref={ref} src={placeholderImage} alt={imageProps.alt} />}
						actual={({ imageProps }) => <img {...imageProps} width="100%" alt="" />}
					/>
				</div>
				<Heading
					level={4}
					content={data.name}
					subheader={
						<Row type="flex" justify="center">
							<Col lg={20}>
								{data.userRatingTotal ? (
									<div className="rating-section">
										<Rating allowHalf disabled defaultValue={data.rating} />
										&nbsp; ({data.userRatingTotal})
									</div>
								) : (
									<div className="rating-section">
										<Trans>No rating yet</Trans>
									</div>
								)}
								{/* <div className="price-section">
                                    <Rating
                                        color="primary"
                                        disabled
                                        allowHalf
                                        defaultValue={data.price_level}
                                        character={<Icon type="dollar" />}
                                    />
                                </div> */}
							</Col>
						</Row>
					}
				/>
			</section>
		</StyledCard>
	)
}

const mapState = ({ place }) => ({ photo: place.photo })

export default connect(mapState)(PlaceCard)
