import { useEffect, useState } from "react"
import Axios from "axios"

const useInternational = () => {
	const [userLocation, setUserLocation] = useState({})

	const fetchUserLocation = async () => {
		const ipAddress = await Axios.get(`https://json.geoiplookup.io/`).then(res => res.data)
		setUserLocation(ipAddress)
	}

	useEffect(() => {
		fetchUserLocation()
	}, [])

	return {
		countryCode: userLocation.country_code,
		countryName: userLocation.country_name,
		isInternational: userLocation.country_code !== "SG" && userLocation.country_code !== "ID"
	}
}

export default useInternational
